@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
* {
  size: 16px;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f7f7f7;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.main-note-container {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  height: 100%;
}

.MuiAppBar-colorPrimary {
  background-color: #f7f7f7 !important;
  box-shadow: none !important;
}

.MuiIconButton-colorInherit {
  color: #1a1b50 !important;
}

.MuiIconButton-colorPrimary {
  color: #444579 !important;
}

.MuiButton-root.lgn-btn {
  color: #ffffff;
  font-weight: 500;
  background-color: #7a7bc7;
  margin-left: 0.5rem;
  padding: 0.5rem 1rem;
}

.MuiButton-root.lgn-btn:hover {
  background-color: #444579;
}

.MuiButton-root.rgst-btn {
  color: #1a1b50;
  font-weight: 500;
  padding: 0.54rem 1rem;
}

.MuiToolbar-root {
  justify-content: flex-end;
}

.main-welcome-container {
  width: 100vw;
}

.main-welcome-section {
  display: flex;
  justify-content: flex-start;
  grid-gap: 10rem;
  gap: 10rem;
  margin-top: 5rem;
}

.main-img {
  width: 50rem;
  float: left;
}

.main-section-text-container {
  margin-left: -5rem;
  align-self: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.main-section-text {
  color: #1a1b50;
  font-size: 3rem;
  display: block;
}

.main-section-subtext {
  color: #444589;
  text-align: left;
}

.main-section-text-border-bottom {
  margin-top: 1rem;
  height: 0.5rem;
  width: 90%;
  background-color: #f9c232;
}

.MuiAppBar-colorPrimary.user-app-bar {
  background-color: #1a1b50 !important;
}

.user-app-bar .MuiIconButton-colorInherit {
  color: #f9c232 !important;
}

.user-app-bar .MuiIconButton-colorPrimary {
  color: #ffffff !important;
}

.MuiToolbar-regular {
  box-shadow: 0px 1px 10px -1px rgba(0, 0, 0, 0.51) !important;
}

.sub-header {
  width: 100%;
  background-color: #f7f7f7;
  padding: 0.5rem 0.5rem 0 0.5rem;
  border-bottom: 0px solid #d5d3da;
  display: flex;
  flex-direction: row-reverse;
}

p {
  margin: 0;
}

.todo-btn {
  height: 1rem !important;
  width: 1rem !important;
  padding: 1rem !important;
  border-radius: 90px !important;
}

.look-up-btn {
  height: 1rem !important;
  width: 1rem !important;
  padding: 1rem !important;
  border-radius: 90px !important;
}

.button-container {
  display: flex;
  flex-direction: row;
}

h6 {
  margin: 0;
}

.note-input-container {
  /* width: 50%; */
  flex-grow: 4;
  margin: 0.5rem;
  height: 77vh;
  border: 1px solid #d5d3da;
  border-radius: 10px;
  background-color: #fdfbfb;
}

.note-input {
  align-self: baseline;
  max-width: 100%;
  max-height: 98vh;
  padding: 1rem !important;
  border: none !important;
  border-radius: 10px;
  line-height: 1.5rem !important;
  background-color: #fdfbfb;
}

.note-utility-container {
  width: 30%;
  margin: 0.5rem 0.5rem 0.5rem 0;
  height: 77vh;
  border: 1px solid #d5d3da;
  border-radius: 10px;
  background-color: #fdfbfb;
}

.hidden {
  display: none;
}

.todo-content {
  display: flex;
  align-items: center;
  padding: 0.5rem !important;
  border: 1px solid #d5d3da;
  border-radius: 10px;
  margin-bottom: 0.5rem;
  box-shadow: 0px 2px 10px -2px rgba(0, 0, 0, 0.05);
}

.todo-text {
  font-size: 1rem;
}

.todo-inactive {
  text-decoration: line-through;
  color: gray !important;
}

.todo-list-container {
  display: flex;
  flex-direction: column;
}

.todo-list-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.todo-list-header span {
  padding: 0.5rem 0 0 1.8rem !important;
  /* margin: 0 0 -0.9rem 1.2rem !important; */
}

.create-todo-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  align-self: flex-end;
  border: 0;
  max-width: 100px;
  height: auto;
  margin: 0.5rem !important;
  height: 1rem !important;
  width: 1rem !important;
  padding: 1rem 1rem 0.5rem 1rem !important;
  border-radius: 90px !important;
}

.create-todo-btn span {
  padding: 0 !important;
}

.todo-list {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0.5rem 0 0.5rem;
}

